import { useState } from "react";

type Room = "livingRoom" | "kitchen" | "bedroom" | "bathroom" | "dining";

interface FormState {
  livingRoom: number;
  kitchen: number;
  bedroom: number;
  bathroom: number;
  dining: number;
  packageType: string;
  includeFurniture?: boolean;
}

const steps = [
  "Rooms to Design",
  "Design Package",
  "Additional Options",
  "Estimate & Details"
];

export default function MultiStepEstimateForm() {
  const [currentStep, setCurrentStep] = useState(0);
  const [form, setForm] = useState<FormState>({
    livingRoom: 1,
    kitchen: 1,
    bedroom: 1,
    bathroom: 1,
    dining: 1,
    packageType: "",
    includeFurniture: false,
  });

  // Strict typing with Room union type!
  const handleIncrement = (room: Room) =>
    setForm((prev) => ({ ...prev, [room]: prev[room] + 1 }));

  const handleDecrement = (room: Room) =>
    setForm((prev) => ({ ...prev, [room]: Math.max(1, prev[room] - 1) }));

  const handleChange = (
    e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>
  ) => {
    const target = e.target;
    const name = target.name;
  
    if (target instanceof HTMLInputElement) {
      if (target.type === "checkbox") {
        setForm((prev) => ({
          ...prev,
          [name]: target.checked,
        }));
      } else {
        // For input type other than checkbox (e.g. text, number)
        setForm((prev) => ({
          ...prev,
          [name]: target.value,
        }));
      }
    } else if (target instanceof HTMLSelectElement) {
      setForm((prev) => ({
        ...prev,
        [name]: target.value,
      }));
    }
  };
  

  // Dummy estimate calculation (customize as needed!)
  const calculateEstimate = () => {
    let pricePerRoom = {
      livingRoom: 20000,
      kitchen: 18000,
      bedroom: 15000,
      bathroom: 12000,
      dining: 16000,
    };
    let base =
      form.livingRoom * pricePerRoom.livingRoom +
      form.kitchen * pricePerRoom.kitchen +
      form.bedroom * pricePerRoom.bedroom +
      form.bathroom * pricePerRoom.bathroom +
      form.dining * pricePerRoom.dining;

    // Package add-ons
    if (form.packageType === "premium") base *= 1.2;
    if (form.packageType === "luxury") base *= 1.5;

    // Additional option
    if (form.includeFurniture) base += 15000;

    return base;
  };

  return (
    <div className="max-w-xl mx-auto mt-10 bg-white p-8 rounded-xl shadow-lg">
      {/* Stepper */}
      <ol className="flex items-center mb-8">
        {steps.map((label, idx) => (
          <li
            key={label}
            className={`flex-1 flex items-center justify-center 
              ${idx <= currentStep ? "text-blue-600 font-bold" : "text-gray-400"} 
              relative`}>
            <span className="z-10 px-2 py-1">{label}</span>
            {idx < steps.length - 1 && <span className="w-full h-1 bg-gray-200 mx-2"></span>}
          </li>
        ))}
      </ol>

      {/* Step Content */}
      {currentStep === 0 && (
        <div>
          <h2 className="text-xl font-semibold mb-4">Select the rooms you'd like us to design</h2>
          {(["livingRoom", "kitchen", "bedroom", "bathroom", "dining"] as Room[]).map((room) => (
            <div key={room} className="flex items-center justify-between py-2">
              <span className="capitalize">{room.replace(/([A-Z])/g, ' $1')}</span>
              <div className="flex items-center">
                <button
                  onClick={() => handleDecrement(room)}
                  className="bg-gray-200 rounded px-2 font-bold text-lg"
                >-</button>
                <span className="mx-2">{form[room]}</span>
                <button
                  onClick={() => handleIncrement(room)}
                  className="bg-gray-200 rounded px-2 font-bold text-lg"
                >+</button>
              </div>
            </div>
          ))}
        </div>
      )}

      {currentStep === 1 && (
        <div>
          <h2 className="text-xl font-semibold mb-4">Choose Design Package</h2>
          <select
            name="packageType"
            value={form.packageType}
            onChange={handleChange}
            className="w-full border rounded px-3 py-2"
          >
            <option value="">Select a package</option>
            <option value="basic">Basic</option>
            <option value="premium">Premium</option>
            <option value="luxury">Luxury</option>
          </select>
        </div>
      )}

      {currentStep === 2 && (
        <div>
          <h2 className="text-xl font-semibold mb-4">Additional Options</h2>
          <label className="block mb-2">
            <input
              type="checkbox"
              name="includeFurniture"
              checked={!!form.includeFurniture}
              onChange={handleChange}
              className="mr-2"
            />
            Include Furniture Selection (+₹15,000)
          </label>
        </div>
      )}

      {currentStep === 3 && (
        <div>
          <h2 className="text-xl font-semibold mb-4">Cost Estimate Summary</h2>
          <div className="bg-gray-100 p-4 rounded mb-2 text-sm">
            <div>
              <span className="font-medium">Rooms: </span>
              {(["livingRoom", "kitchen", "bedroom", "bathroom", "dining"] as Room[]).map((room) => (
                <span key={room} className="mx-1">
                  {room.replace(/([A-Z])/g, ' $1')}: {form[room]}
                </span>
              ))}
            </div>
            <div>
              <span className="font-medium">Package: </span>
              {form.packageType ? form.packageType.charAt(0).toUpperCase() + form.packageType.slice(1) : "Not selected"}
            </div>
            <div>
              <span className="font-medium">Furniture: </span>
              {form.includeFurniture ? "Yes" : "No"}
            </div>
            <div className="mt-4 text-lg font-bold text-green-600">
              Estimated Total: ₹{calculateEstimate().toLocaleString()}
            </div>
          </div>
          <button className="bg-blue-600 text-white px-4 py-2 rounded w-full mt-4 hover:bg-blue-700 transition">
            Submit Estimate Request
          </button>
        </div>
      )}

      {/* Navigation buttons */}
      <div className="flex justify-between mt-8">
        <button
          onClick={() => setCurrentStep((prev) => Math.max(prev - 1, 0))}
          disabled={currentStep === 0}
          className="bg-gray-200 px-4 py-2 rounded disabled:opacity-50"
        >
          Back
        </button>
        <button
          onClick={() => setCurrentStep((prev) => Math.min(prev + 1, steps.length - 1))}
          disabled={currentStep === steps.length - 1}
          className="bg-blue-600 text-white px-4 py-2 rounded disabled:opacity-50"
        >
          Next
        </button>
      </div>
    </div>
  );
}
